import React,{Component} from 'react';
import {Redirect} from 'react-router-dom';
import axios from 'axios';
import {message} from 'antd';
import './style.css'

class Vip extends Component{
    constructor(props){
        super(props);
        this.state={
            login:true,
            fetchStatus:false
        }
    }
    render(){
        if(this.state.login){
            if(this.state.fetchStatus){
                return <div className='vip'>Vip</div>
            }else{
                return <div className='vip'>checking your information...</div>
            }
            
        }else{
            return <Redirect to='/'/> 
        }
    }
    
    componentDidMount(){
        axios.get('http://www.dell-lee.com/react/api/isLogin.json',{withCredentials:true})
        .then(res=>{
            this.setState({
                login:res.data.data.login,
                fetchStatus:true
            },()=>{
                if(!this.state.login){
                    message.error('Please login!');
                }
            })
        })
        
    }
}

export default Vip;