import React,{Component} from 'react';
import {BrowserRouter, Route, Switch} from 'react-router-dom';
import ReactDOM from 'react-dom';
import { Layout } from 'antd';
import NewsList from './Container/List/';
import Detail from './Container/Detail/';
import AppHeader from './Component/Header/index.js';
import Login from './Component/Login/index.js';
import Vip from './Container/Vip/'
import 'antd/dist/antd.css';
import './style.css'

const { Header, Footer, Content } = Layout;

class App extends Component{
    render(){
        return(
          <BrowserRouter>
            <Layout style={{minWidth:1300}}>
              <Header className='header'>
                <AppHeader />
              </Header>
              <Content className='content'>
                <Login />
                <Switch>
                  <Route path='/vip' component={Vip}/>
                  <Route path='/detail/:id' component={Detail}/>
                  <Route path='/:id?' component={NewsList}/>
                </Switch>
              </Content>
              <Footer className='footer'>@copyright LiangGao 2018</Footer>
            </Layout>
          </BrowserRouter>
          )
    }
}

ReactDOM.render(<App />, document.getElementById('root'));

