import React,{Component} from 'react';
import {Link, withRouter} from 'react-router-dom';
import axios from 'axios';
import { Button, Modal, Input, message} from 'antd';

class Login extends Component{
    constructor(props){
        super(props);
        this.state={
            login:false,
            visible:false,
            user:'',
            psw:''
        }
        this.showLogin=this.showLogin.bind(this)
        this.hideLogin=this.hideLogin.bind(this)
        this.inputUser=this.inputUser.bind(this)
        this.inputPsw=this.inputPsw.bind(this)
        this.checkLogin=this.checkLogin.bind(this)
        this.logOut=this.logOut.bind(this)
    }

    showLogin(){
        this.setState({
            visible:true
        })
    }
    hideLogin(){
        this.setState({
            visible:false
        })
    }
    inputUser(e){
        this.setState({
            user:e.target.value
        })
    }
    inputPsw(e){
        this.setState({
            psw:e.target.value
        })
    }
    checkLogin(){
        const{user,psw}=this.state;
        let url='http://www.dell-lee.com/react/api/login.json';
        axios.get(url+'?user='+user+'&password='+psw,{withCredentials:true})
        .then(res=>{
            let status=res.data.data.login;
            if(status){
                message.success('login success!');
                this.setState({
                    login:true
                })
                setTimeout(this.hideLogin(),6000);
            }else{
                message.error('login failed!');

            }
        })
    }
    logOut(){
        axios.get('http://www.dell-lee.com/react/api/logout.json',{withCredentials:true})
        .then(res=>{
            if(res.data.data.logout){
                this.setState({
                    login:false
                })
                message.success('you have signed out!');
                this.props.history.push('');
            }
            
        })
    }

    render(){
        return (
            <div  style={{marginBottom:10}}>
                {
                   this.state.login?  
                   <Button 
                    type="primary" 
                    onClick={this.logOut}
                    >Sign out
                   </Button>:
                    <Button 
                      type="primary"                 
                      onClick={this.showLogin}
                    >
                      Login
                    </Button>
                }
                    <Link to='/vip'>
                        <Button 
                          type="primary"
                          style={{marginLeft:10}}
                        >
                          Vip
                        </Button>
                    </Link>
                <Modal
                  title="Login"
                  visible={this.state.visible}
                  onOk={this.checkLogin}
                  onCancel={this.hideLogin}
                >
                  <Input 
                      placeholder="Please input your username"
                      style={{marginBottom:10}}
                      value={this.state.user} 
                      onChange={this.inputUser}
                  />
                  <Input 
                      placeholder="Please input your password" 
                      type='password' 
                      value={this.state.psw} 
                      onChange={this.inputPsw}
                  />
                </Modal>
            </div>
        ) 
    }

    componentDidMount(){
        axios.get('http://www.dell-lee.com/react/api/isLogin.json',{withCredentials:true})
        .then(res=>{
            this.setState({
                login:res.data.data.login
            })
        })
    }
}

export default withRouter(Login);